export default {
	data() {
		return {
			scrollid: '',
			list: ['A1', 'A2', 'A3', 'A4', 'A5', 'A6', 'A7', 'A8', 'A9', 'A10', 'A11', 'A12', 'A13', 'A14', 'A15', 'A16', 'A17',
				'A18', 'A19'
			],
			images: [{
				url: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/resultsbg2.png'
			}, {
				url: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/resultsbg2.png'
			}, {
				url: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/resultsbg2.png'
			}],
			// canvas
			nickName: '测试名',
			avatarUrl: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/list1.png',
			score: '99', //分数
			rank_title: '点赞点赞', //右上角标题
			rank_content: '帅呆了帅呆了帅呆了帅呆了帅呆了帅呆了帅呆了帅呆了帅呆了帅呆了', //内容
			permission: '所有解释权...',
			img1: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/resultsbg2.png', //预先下载的图片
			img2: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/score.png',
			img3: 'https://static.epetbar.com/static_web/wap/dist/images/asiaPet/tx.png', //二维码图片
		}
	},
	onLoad() {

	},
	onShow() {
		this.drawCanvas();
	},
	methods: {
		/**
		 * scroll-into-view滚动位置案例
		 */
		viewScroll: function(e) {
			let _this = this;
			let id = e.currentTarget.dataset.id;
			let list1 = ['A1', 'A2', 'A3', 'A4', 'A5', 'A6', 'A7', 'A8', 'A9', 'A10', 'A11', 'A12', 'A13', 'A14', 'A15', 'A16',
				'A17', 'A18', 'A19'
			];
			let list2 = ['B1', 'B2', 'B3', 'B4', 'B5'];
			// let list3 = ['C1', 'C2', 'C3', 'C4', 'C5'];
			let list3 = ['C1', 'C2', 'C3', 'C4', 'C5', 'C6', 'C7', 'C8', 'C9', 'C10', 'C11', 'C12', 'C13', 'C14', 'C15', 'C16',
				'C17', 'C18', 'C19'
			];

			if (id == 1) {
				this.list = list1;
			} else if (id == 2) {
				this.list = list2;
			} else {
				this.list = list3;
			}
			_this.scrollid = 'demo1';
			// this.scrollid = '';
			// setTimeout(function() {
			// 	_this.scrollid = 'demo1';
			// }, 10);
		},
		/**
		 * canva画布
		 */
		drawCanvas: function() {
			var that = this;
			const ctx = uni.createCanvasContext('myCanvas');

			let res = uni.getSystemInfoSync();
			// let rpxnum = res.windowWidth / 750;
			let rpxnum = 0.5;
			let windowwidth = res.windowWidth;

			var widthImage = 690 * rpxnum;
			var heightImage = widthImage * 1.3;

			//绘制背景图片
			ctx.drawImage(this.img1, 30 * rpxnum, 0, widthImage, heightImage)
			//绘制头像
			ctx.save();
			ctx.beginPath();
			ctx.arc(rpxnum * 130, rpxnum * 120, rpxnum * 60, 0, 2 * Math.PI);
			ctx.clip();
			ctx.drawImage(this.avatarUrl, rpxnum * 70, rpxnum * 60, rpxnum * 120, rpxnum * 120);
			ctx.restore();
			//绘制用户名
			ctx.fillStyle = '#333333';
			// ctx.font = "16px bold Arial";
			ctx.setFontSize(32 * rpxnum)
			ctx.fillText(this.nickName, rpxnum * 220, rpxnum * 130);
			//绘制标题
			ctx.rect(widthImage - 240 * rpxnum, 22 * rpxnum, rpxnum * 186, rpxnum * 113);
			// ctx.fillStyle = '#fff'; //头条颜色值不能简写
			// ctx.font = "15px bold Arial";
			ctx.setFillStyle('#ffffff'); //头条颜色值不能简写
			ctx.setFontSize(30 * rpxnum)
			ctx.setTextAlign('center');
			ctx.setTextBaseline('middle')
			ctx.fillText(this.rank_title, widthImage - 170 * rpxnum, rpxnum * 75);
			// 绘制分数
			ctx.drawImage(this.img2, (widthImage - 146) * rpxnum, 200 * rpxnum, rpxnum * 292, rpxnum * 80);
			// ctx.fillStyle = '#ffba3e';
			// ctx.font = "30px bold";
			ctx.setFillStyle('#ffba3e'); //头条颜色值不能简写
			ctx.setFontSize(60 * rpxnum)
			ctx.setTextAlign('center');
			ctx.setTextBaseline('middle')
			ctx.fillText(this.score, widthImage * rpxnum, rpxnum * 240);
			// ctx.font = "15px bold";
			// ctx.font = 'italic bold 15px cursive'
			ctx.setFontSize(30 * rpxnum)
			ctx.fillText("分", (widthImage + 80) * rpxnum, rpxnum * 250);
			//绘制提示
			// ctx.fillStyle = '#b77fec';
			// ctx.font = "11px bold Arial";
			ctx.setFillStyle('#b77fec'); //头条颜色值不能简写
			ctx.setFontSize(22 * rpxnum)
			ctx.setTextAlign('center');
			ctx.fillText(this.permission, widthImage / 2, heightImage - 70 * rpxnum);
			//绘制二维码
			ctx.drawImage(this.img3, rpxnum * 520, rpxnum * 680, rpxnum * 100, rpxnum * 100);
			ctx.draw();
		},
		save: function() {
			var _this = this;
			uni.canvasToTempFilePath({
				canvasId: 'myCanvas',
				success: function(res) {
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function(res2) {
							uni.showToast({
								title: '图片保存成功',
								icon: 'none',
								duration: 2000,
								mask: true
							})
						},
						fail: function() {
							uni.showToast({
								title: '图片保存失败,请重试',
								icon: 'none',
								duration: 2000,
								mask: true
							})
						}
					})
				},
				fail: function() {
					uni.showToast({
						title: '图片保存失败,请重试',
						icon: 'none',
						duration: 2000,
						mask: true
					})
				}
			})
		}
	}
}
